@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.App{background-color:#222;min-height:100vh;font-family:'Roboto', sans-serif;position:relative}.fetching-spinner{position:absolute;top:180px;left:50%;-webkit-transform:translate(0, -50%);transform:translate(0, -50%);z-index:100}

.search-input-wrapper{margin:auto;padding:20px;background-color:#222;border-bottom:1px solid #cacaca;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:flex-start;align-items:flex-start}.search-input-wrapper .search-input{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center}.search-input-wrapper .search-input>*{-webkit-flex-wrap:nowrap !important;flex-wrap:nowrap !important}.search-input-wrapper .input{margin-right:5px;border-radius:2px}.search-input-wrapper button{border-radius:2px;margin-right:5px}.search-input-wrapper .dropdown button{min-width:60px}.search-filters{width:100%;padding:10px 20px;color:#fff;display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;font-size:1.2rem;border-radius:2px;background-color:#1b1b1b}.search-filters button{outline:none;background-color:#3f3f3f;display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center;color:#fff;border:none;padding:8px;border-radius:2px;font-size:0.7rem;min-width:60px}.search-filters button:nth-child(2){margin-left:auto}.search-filters button:nth-child(3){margin-left:5px}.search-filters .view-item{padding:2px 10px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;margin-left:5px;font-size:0.9rem;background-color:#3f3f3f;border-radius:2px;cursor:pointer;opacity:1}.search-filters .view-item-selected{opacity:0.5}.search-filters .order-by-button{margin-left:5px}.search-filters .order-by-button svg{margin-left:5px}.search-filters .button-selected{opacity:0.5}@media (max-width: 600px){.search-input-wrapper{padding:10px}.search-filters{padding:10px}}

.videos-container{padding:20px}@media (max-width: 600px){.videos-container{padding:0px}}

.video-item-container{font-family:'Roboto', sans-serif;padding:10px 0px;border-radius:4px;margin:auto;color:#fff;display:-webkit-flex;display:flex}.video-info{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:flex-start;align-items:flex-start;margin-right:auto}.video-info .video-title{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:flex-start;align-items:flex-start;margin-bottom:10px}.video-info .video-title h4{font-size:1.3rem;margin-bottom:0px;margin-bottom:8px}.video-info .video-tags{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;margin-bottom:10px}.video-info .video-tags p{margin:0;font-size:0.7rem;color:#fff;padding:4px;border-radius:2px;background-color:#535353;margin-right:10px}.video-info .video-tags .source-youtube{background-color:#f04d4d}.video-info .video-tags .source-vimeo{background-color:#2c7ec2}.video-info .video-desc{margin-bottom:10px;font-size:0.8rem;color:#adadad}.video-info .remove-video-btn{padding:3px;font-size:0.8rem}.video-info .video-stats{width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;font-size:0.9rem;margin-top:auto;margin-bottom:10px}.video-info .video-stats .stat-box{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;margin-right:20px}.video-info .video-stats .stat-box svg{color:#f04d4d}.video-info .video-stats .stat-box p{margin:0;margin-left:5px;color:#fff}.row{width:100%}.favorite-box{margin:auto 20px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;font-size:2rem;color:#fff;cursor:pointer}.favorite-box svg:hover{color:#f04d4d}.play-button{margin:auto;font-size:4.5rem;color:#fff;position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);margin:0;box-sizing:border-box}.hidden{display:none}.video-item-list{width:100%;display:-webkit-flex;display:flex;border-bottom:1px solid #3f3f3f}.video-item-list .video-thumbnail{margin-bottom:20px;position:relative;cursor:pointer}.video-item-list img{border:1px solid #3f3f3f;width:100%;height:auto}.video-item-list .video-info-container{width:100%;height:100%;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.video-item-grid{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:flex-start;justify-content:flex-start;padding:6px;box-sizing:border-box;width:100%;background-color:#1d1d1d;border:1px solid #3f3f3f}.video-item-grid .video-thumbnail{width:100%;height:auto;margin-bottom:5px;cursor:pointer;background-color:#3f3f3f;position:relative}.video-item-grid .video-thumbnail img{border:1px solid #3f3f3f;object-fit:cover;height:150px;width:100%}.video-item-grid .video-info-container{display:inline-block;font-size:15px;width:100%;overflow:visible;overflow:initial;white-space:normal}.video-item-grid .video-info-container .video-title{width:100%}.video-item-grid .video-info-container .video-title h4{width:100%;font-size:0.8rem;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.video-item-grid .video-info-container .video-stats{font-size:0.8rem;width:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:flex-start;align-items:flex-start;margin-bottom:0}.video-item-grid .video-info-container .video-stats .stat-box:nth-child(1){margin-bottom:4px}.video-item-grid .favorite-box{width:100%;font-size:1.4rem;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin-bottom:5px}


